import React, { Component } from 'react'
import Button from '@material-ui/core/Button'
import DangerButton from './components/DangerButton'


const styles = ({
  container: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column'
  }
})

export default class IndexPage extends Component {
  constructor(props) {
    super(props)
    this.state = { startGame: true }

    this.onClickStartGame = this.onClickStartGame.bind(this)
  }

  onClickStartGame() {
    console.log("Game Starting")
    this.setState()
  }
  render() {

    // const displayButton = this.state.startGame? <Button variant="contained" onClick={this.onClickStartGame} >
    // Start Game</Button>: <DangerButton text="End Game"></DangerButton>
    console.log("Rendering");



    return (
      <div style={styles.container}>
        {this.state.startGame && <Button variant="contained" onClick={this.onClickStartGame} >
          Start Game</Button>}
        {!this.state.startGame && <DangerButton text="End Game"></DangerButton>}
      </div>
    )
  }
}
